/*===== global styles =====*/

*,
*::before,
*::after {
    box-sizing: border-box;
    // min-height: 0.001vw; // â€” Fixes Safari bug with viewport units in calc()
}

html {
    min-width: 320px;
    scrollbar-gutter: stable;
    scroll-behavior: smooth;
}

body {
    width: 100%;
    height: 100%;
    min-height: 100vh;
    min-width: 320px;
    display: flex;
    flex-direction: column;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: var(--color-dark, #1a1a1a);
    font-family: $font_main;
    font-weight: 400;
    background-color: var(--color-white, #fff);
}

body.lock {
    overflow: hidden;
}

.container {
    width: 100%;
    max-width: 1580px;
    margin: 0 auto;
    padding: 0 40px;

    @include xs {
        padding: 0 20px;
    }
}

