:root {
  --color-dark: #090909;
  --color-green: #41a62b;
  --color-green-bg: #40a22b;
  --color-white: #fff;
  --color-white-fc: #fcfcfc;
  --color-white-fb: #fbfbfb;
  --color-grey: #a1a1a1;
  --color-grey-13: #131313;
}

@font-face {
  font-family: "Grtsk Peta";
  src: url("../fonts/GrtskPeta-Semibold.woff2") format("woff2"), url("../fonts/GrtskPeta-Semibold.woff") format("woff"), url("../fonts/GrtskPeta-Semibold.ttf") format("truetype");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Grtsk Peta";
  src: url("../fonts/GrtskPeta-Regular.woff2") format("woff2"), url("../fonts/GrtskPeta-Regular.woff") format("woff"), url("../fonts/GrtskPeta-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
.section {
  margin-bottom: 190px;
}
@media (max-width: 1200px) {
  .section {
    margin-bottom: 120px;
  }
}
@media (max-width: 767px) {
  .section {
    margin-bottom: 90px;
  }
}

.section__title {
  margin: 0 0 44px 0;
  font-weight: 600;
  font-size: 75px;
  line-height: 116%;
}
@media (max-width: 1200px) {
  .section__title {
    font-size: 50px;
  }
}
@media (max-width: 767px) {
  .section__title {
    margin-bottom: 35px;
    font-size: 35px;
  }
}

/*===== global styles =====*/
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  min-width: 320px;
  scrollbar-gutter: stable;
  scroll-behavior: smooth;
}

body {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  min-width: 320px;
  display: flex;
  flex-direction: column;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--color-dark, #1a1a1a);
  font-family: "Grtsk Peta", sans-serif;
  font-weight: 400;
  background-color: var(--color-white, #fff);
}

body.lock {
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1580px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 767px) {
  .container {
    padding: 0 20px;
  }
}

.header {
  background-color: var(--color-green-bg, #40a22b);
  background: linear-gradient(90deg, #194011 0.26%, #40a32a 48.7%, #194011 100%);
}

.header__container {
  max-width: 1734px;
  min-height: 137px;
  display: flex;
  align-items: center;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 991px) {
  .header__container {
    min-height: 75px;
    padding: 0 20px;
  }
}

.header__logo {
  display: block;
  width: 425px;
  height: auto;
  transform: translateY(-10px);
}
@media (max-width: 991px) {
  .header__logo {
    width: 244px;
  }
}

.main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.intro {
  margin-bottom: 150px;
}
@media (max-width: 1200px) {
  .intro {
    margin-bottom: 100px;
  }
}
@media (max-width: 767px) {
  .intro {
    margin-bottom: 80px;
  }
}

.intro__container {
  position: relative;
  max-width: 2400px;
  margin: 0 auto;
}
.intro__container::after {
  content: "";
  display: block;
  width: 100%;
  padding-top: 38.85%;
}
@media (max-width: 575px) {
  .intro__container::after {
    padding-top: 99.23%;
  }
}
.intro__container picture {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: block;
}
.intro__container img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.info__title {
  max-width: 1360px;
  margin: 0;
  line-height: 116%;
}
@media (max-width: 767px) {
  .info__title {
    margin-top: 9px;
    line-height: 72%;
  }
}
.info__title span:nth-child(1) {
  display: block;
  margin-bottom: 23px;
  font-weight: 600;
  font-size: 75px;
}
@media (max-width: 1200px) {
  .info__title span:nth-child(1) {
    font-size: 50px;
  }
}
@media (max-width: 767px) {
  .info__title span:nth-child(1) {
    margin-bottom: 22px;
    font-size: 35px;
  }
}
.info__title span:nth-child(2),
.info__title span:nth-child(3) {
  font-weight: 400;
  font-size: 30px;
}
@media (max-width: 1200px) {
  .info__title span:nth-child(2),
  .info__title span:nth-child(3) {
    font-size: 25px;
  }
}
@media (max-width: 767px) {
  .info__title span:nth-child(2),
  .info__title span:nth-child(3) {
    font-size: 20px;
  }
}
.info__title span:nth-child(2) {
  color: #41a62b;
}

.geo__top {
  padding: 38px 0 44px;
  background-color: var(--color-grey, #a1a1a1);
}
@media (max-width: 1200px) {
  .geo__top {
    padding: 30px 0 35px;
  }
}
@media (max-width: 767px) {
  .geo__top {
    padding: 23px 0 31px;
  }
}

.geo__title {
  margin-bottom: 0;
  text-align: center;
  color: var(--color-white-fc, #fcfcfc);
}
@media (max-width: 767px) {
  .geo__title {
    text-align: left;
  }
}

.geo__map {
  margin: 92px 0;
}
@media (max-width: 767px) {
  .geo__map {
    margin: 40px 0 42px;
  }
}

.geo__img {
  position: relative;
}
.geo__img::after {
  content: "";
  display: block;
  width: 100%;
  padding-top: 60.33%;
}
.geo__img img {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.geo__bottom {
  padding: 103px 0 108px;
  background-color: var(--color-green-bg, #40a22b);
  background: linear-gradient(90deg, #194011 0.26%, #40a32a 48.7%, #194011 100%);
}
@media (max-width: 1200px) {
  .geo__bottom {
    padding: 60px 0 65px;
  }
}
@media (max-width: 767px) {
  .geo__bottom {
    padding: 32px 0 36px;
  }
}

.geo__bottom .container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 35px;
}
@media (max-width: 1600px) {
  .geo__bottom .container {
    max-width: 950px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 31px 26px;
  }
}
@media (max-width: 767px) {
  .geo__bottom .container {
    max-width: 420px;
  }
}

.geo__link {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  gap: 24px;
  text-decoration: none;
}
@media (max-width: 575px) {
  .geo__link {
    display: block;
  }
}
@media (hover: hover) {
  .geo__link:hover .geo__icon {
    transform: scale(1.05);
    box-shadow: 0 0 10px 0 var(--color-white, #fff);
  }
}

.geo__icon {
  flex: 0 0 97px;
  display: block;
  width: 97px;
  height: 97px;
  border-radius: 50%;
  overflow: hidden;
  transition: transform 0.2s ease-out, box-shadow 0.2s ease-out;
}
.geo__icon img {
  display: block;
  width: 100%;
  height: auto;
}
@media (max-width: 1200px) {
  .geo__icon {
    flex: 0 0 72px;
    width: 72px;
    height: 72px;
  }
}
@media (max-width: 767px) {
  .geo__icon {
    flex: 0 0 47px;
    width: 47px;
    height: 47px;
    margin-bottom: 12px;
  }
}

.geo__descr {
  display: block;
}

.geo__name {
  display: block;
  margin-bottom: 5px;
  font-weight: 400;
  font-size: 30px;
  line-height: 116%;
  color: var(--color-white, #fff);
}
@media (max-width: 1200px) {
  .geo__name {
    font-size: 24px;
  }
}
@media (max-width: 767px) {
  .geo__name {
    font-size: 16px;
  }
}
@media only screen and (max-width: 370px) {
  .geo__name {
    font-size: 14px;
  }
}

.geo__rating {
  width: 181px;
  height: 32px;
  background: url("../images/geo/rating_stars.svg") center/contain no-repeat;
}
@media (max-width: 1200px) {
  .geo__rating {
    width: 139px;
    height: 24px;
  }
}
@media (max-width: 767px) {
  .geo__rating {
    width: 99px;
    height: 17px;
  }
}

.production__container {
  max-width: 1580px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 767px) {
  .production__container {
    padding: 0;
  }
}

.production__wrapper {
  position: relative;
  overflow: hidden;
  min-height: 218px;
}
.production__wrapper::after {
  content: "";
  display: block;
  width: 100%;
  padding-top: 56%;
}

.production__preview,
.production__video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.production__preview {
  z-index: 3;
}
.production__preview picture,
.production__preview img {
  display: block;
  width: 100%;
  height: 100%;
}
.production__preview picture {
  transition: filter 0.2s ease-out;
}
.production__preview img {
  object-fit: cover;
}

.production__video {
  z-index: 2;
}
.production__video .video-iframe {
  display: block;
  width: 100%;
  height: 100%;
}

.production__btn {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 4;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  background-color: transparent;
  padding: 0;
  border: none;
  outline: none;
  appearance: none;
}
.production__btn svg {
  display: block;
  width: 128px;
  height: 128px;
}
@media (max-width: 1200px) {
  .production__btn svg {
    width: 92px;
    height: 92px;
  }
}
@media (max-width: 767px) {
  .production__btn svg {
    width: 46px;
    height: 46px;
  }
}
@media (hover: hover) {
  .production__btn:hover + picture {
    filter: blur(5px);
  }
}

.preview__descr {
  position: absolute;
  bottom: 70px;
  left: 0;
  max-width: 997px;
  padding: 30px 80px;
}
.preview__descr::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, #194011 0.26%, #40a32a 48.7%, #194011 100%);
  opacity: 0.8;
}
.preview__descr p {
  padding: 0;
  margin: 0;
  position: relative;
  z-index: 2;
  font-weight: 600;
  font-size: 85px;
  line-height: 111%;
  color: var(--color-white, #fff);
}
@media (max-width: 1600px) {
  .preview__descr {
    padding: 20px 50px;
  }
  .preview__descr p {
    font-size: 60px;
  }
}
@media (max-width: 1300px) {
  .preview__descr {
    bottom: 60px;
  }
  .preview__descr p {
    font-size: 50px;
  }
}
@media (max-width: 1200px) {
  .preview__descr {
    bottom: 50px;
    max-width: 700px;
    padding: 20px 40px;
  }
}
@media (max-width: 991px) {
  .preview__descr {
    bottom: 30px;
    max-width: 500px;
    padding: 15px 30px;
  }
  .preview__descr p {
    font-size: 32px;
  }
}
@media (max-width: 767px) {
  .preview__descr {
    bottom: 18px;
    max-width: 260px;
    padding: 7px 21px;
  }
  .preview__descr p {
    font-size: 22px;
  }
}

.platforms__wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}
@media (max-width: 767px) {
  .platforms__wrapper {
    display: block;
  }
}

.platforms__link {
  position: relative;
  display: block;
  overflow: hidden;
  text-decoration: none;
}
.platforms__link:after {
  content: "";
  display: block;
  width: 100%;
}
.platforms__link img {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.4s ease-out;
}
.platforms__link.platforms__link--wide {
  grid-column: 1/span 2;
}
@media (max-width: 767px) {
  .platforms__link:not(:last-child) {
    margin-bottom: 10px;
  }
}
@media (hover: hover) {
  .platforms__link:hover img {
    transform: scale(1.05);
  }
}

.platforms__link--small::after {
  padding-top: 59.32%;
}

.platforms__link--wide::after {
  padding-top: 29.27%;
}

@media (max-width: 767px) {
  .platforms__link--small::after,
  .platforms__link--wide::after {
    padding-top: 59.15%;
  }
}

.platforms__name {
  position: absolute;
  top: 40px;
  left: 40px;
  z-index: 2;
  font-weight: 400;
  font-size: 30px;
  line-height: 116%;
  color: var(--color-white-fb, #fbfbfb);
}
.platforms__name.platforms__name--dark {
  color: var(--color-grey-13, #131313);
}
@media (max-width: 1200px) {
  .platforms__name {
    top: 20px;
    left: 20px;
    font-size: 25px;
  }
}
@media (max-width: 767px) {
  .platforms__name {
    top: 18px;
    left: 19px;
    font-size: 20px;
  }
}
@media (max-width: 575px) {
  .platforms__name {
    font-size: 16px;
  }
}

.platforms__remarks {
  position: absolute;
  bottom: 50px;
  left: 40px;
  z-index: 2;
  max-width: 306px;
  font-weight: 400;
  font-size: 16px;
  line-height: 116%;
  color: var(--color-white, #fff);
}
@media (max-width: 1200px) {
  .platforms__remarks {
    max-width: 280px;
    left: 20px;
    bottom: 20px;
    font-size: 14px;
  }
}
@media (max-width: 767px) {
  .platforms__remarks {
    max-width: 170px;
    left: 16px;
    bottom: 14px;
    font-size: 10px;
  }
}

.footer {
  color: var(--color-white-fc, #fcfcfc);
  background-color: var(--color-green-bg, #40a22b);
  background: linear-gradient(90deg, #194011 0.26%, #40a32a 48.7%, #194011 100%);
}

.footer__container {
  max-width: 1734px;
  min-height: 229px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 35px 40px 64px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 20px;
  grid-template-areas: "logo social phone" "copyrigth policy email";
}
@media (max-width: 991px) {
  .footer__container {
    max-width: 800px;
    min-height: auto;
    grid-template-columns: repeat(2, 1fr);
    grid-template-areas: "logo social" "phone email" "copyrigth policy";
    gap: 30px 20px;
    padding: 12px 20px 29px;
  }
}
@media (max-width: 767px) {
  .footer__container {
    grid-template-columns: 1fr;
    grid-template-areas: "logo" "phone" "email" "social" "copyrigth" "policy";
    justify-content: center;
    justify-items: center;
    gap: 0;
  }
}

.footer__logo {
  grid-area: logo;
}

.footer__copyright {
  grid-area: copyrigth;
}

.footer__social {
  grid-area: social;
}

.footer__policy {
  grid-area: policy;
}

.footer__phone {
  grid-area: phone;
}

.footer__email {
